// container
.container {
  max-width: 1200px;
  width: 98%;
  margin: 0 auto;
  //background: rgba(255,255,255,0.3);
}

// cont__title 
.cont__title {
  padding: 15vmax 1% 10vmax;
  font-size: 5vmax;
  font-family: var(--main_font);
  text-transform: uppercase;
  line-height: 0.85;
  color: var(--white);
  background-color: var(--black);

  em {
      display: block;
  }
}
.cont__title.light {
  background-color: var(--light_bg);
  color: var(--black);
}

@media (max-width: 1200px){
  .cont__title {
      text-align: center;
      padding-bottom: 10vw;
  }
}

